/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  background-color: #f4f4f9;
  color: #333;
  padding: 20px;
}

/* Header */
.header {
  background-color: #23395d;
  color: #fff;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
}

.header .logo {
  width: 50px;
  margin-bottom: 10px;
}

.header h1 {
  font-size: 1.8rem;
  font-weight: 600;
}

.header p {
  font-size: 1rem;
  margin-top: 10px;
}

/* Main Content */
.main-content {
  margin-top: 20px;
}

.services, .booking, .bookings, .customer-bookings {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.services h2, .booking h2, .bookings h2, .customer-bookings h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.services ul, .bookings ul, .customer-bookings ul {
  list-style: none;
}

.services li, .bookings li, .customer-bookings li {
  font-size: 1rem;
  margin: 10px 0;
}

.booking-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.booking-form .form-row {
  display: flex;
  flex-direction: column;
}

.booking-form label {
  font-size: 1rem;
  margin-bottom: 5px;
}

.booking-form input,
.booking-form select,
.booking-form button {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.booking-form input,
.booking-form select {
  width: 100%;
}

.booking-form button {
  background-color: #23395d;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background 0.3s;
}

.booking-form button:hover {
  background-color: #1d2e4a;
}

/* Footer */
.footer {
  text-align: center;
  margin-top: 20px;
  font-size: 0.9rem;
  color: #555;
}

.footer a {
  color: #23395d;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

/* Login */
.login {
  text-align: center;
  margin-top: 50px;
}

.login input, .login button {
  padding: 10px;
  font-size: 1rem;
  margin-top: 10px;
  width: 80%;
  max-width: 400px;
}

.login button {
  background-color: #23395d;
  color: white;
  border: none;
  cursor: pointer;
}

.login button:hover {
  background-color: #1d2e4a;
}

/* Responsive Design */
@media (min-width: 768px) {
  .booking-form {
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;
  }

  .booking-form .form-row {
    flex: 1;
  }

  .booking-form .form-row:not(:last-child) {
    margin-right: 10px;
  }

  .booking-form button {
    width: auto;
    padding: 10px 20px;
  }
}
